* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  border: 1px solid #333;
  margin: 50px auto;
  text-align: center;
  padding: 100px 0;
}

.state button {
  background-color: tomato;
  border: 0;
  padding: 10px 30px;
  margin: 10px;
}

.state input {
  border: 1px solid tomato;
  padding: 10px 30px;
  display: block;
  margin: 20px auto;
  border-radius: 50px;
}

.state input::placeholder {
  font-weight: bold;
  color: #333;
}

.state input:focus {
  outline: none;
}